import uParse from '@/components/gaoyia-parse/parse.vue';
import mpvueCityPicker from '../../components/mpvue-citypicker/mpvueCityPicker.vue'
export default {
	data() {
		return {
			type:'',
			themeColor: '#181818',
			cityPickerValueDefault: [0, 0, 1],
			city:'',
			bannerIndex:1,
			content:'商品详情'
		}
	},
	onShareAppMessage() {
		var _this = this;
		return {
			title: "中型规模公司代理注册服务业务",
			path: '/pages/index/index?pageNeedId1='+_this.uid+"&pageNeedId2="+_this.goodId+"&page=goods_info"
		}
	},
	components: {
		uParse,
		mpvueCityPicker
	},
	onLoad(e) {
		this.type = e.type;
	},
	methods: {
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			this.city = e.label.replace(/-/g,' ');
		},
		onCancel(e) {
			this.city = e.label.replace(/-/g,' ');
		},
		change(e) {
			this.bannerIndex = e.detail.current+1;
		},
		confirmOrder() {
			var _this = this;
			if(this.type==1){
				uni.navigateTo({
					url:'../order_info/order_info?status=0'
				})
			} else if(this.type==2){
				uni.navigateTo({
					url:'../order_info_dzl/order_info_dzl?status=0'
				})
			} else if(this.type==3){
				uni.navigateTo({
					url:'../order_cpl/order_cpl'
				})
			} else if(this.type==4){
				uni.navigateTo({
					url:'../order_info_sbl/order_info_sbl'
				})
			}
		}
	}
}
